import React from "react"
import { graphql } from "gatsby"
import {
  Header,
  Container,
  Parallax,
  Paragraph,
  Row,
  Col,
  Heading,
  Center,
} from "../components/index"

export default ({ data: { banner } }) => (
  <>
    <Parallax image={banner.childImageSharp.fluid} height="full" />
    <Header />
    <Center>
      <Heading>Chapter One</Heading>
      <h1>The Storm</h1>
      <Container text>
        <Paragraph indent>
          Isac knew his brother still lived. He just knew it. His hands shook as
          he drove down the desert road. He knew it, and he had told them— and
          yet they’d insisted on holding a funeral. A funeral with no body to
          bury. Isac’s shoulders shook in condemnation and fury. How could they
          give up like that? The setting sun painted the landscape and his
          window in a deep red hue. Isac cursed and tried accessing the GPS on
          his phone, though the fog of anger made it hard to focus.
        </Paragraph>
      </Container>
    </Center>
  </>
)

export const query = graphql`
  query {
    banner: file(relativePath: { eq: "forside-tittellogo.jpg" }) {
      childImageSharp {
        fluid(quality: 90, maxWidth: 1080) {
          ...GatsbyImageSharpFluid_withWebp
        }
      }
    }
  }
`
